<template>
  <v-app id="inspire">

    <v-navigation-drawer
      app
      v-model="drawer"
    >
      <v-list-item>
        <v-list-item-content>

          <v-list-item-title 
            class="title"
            v-if="!!user.firstname"
          >
            {{ user.firstname }}'s Projects 
          </v-list-item-title>
          <v-list-item-title v-else>
            {{ user.username }}'s Projects
          </v-list-item-title>
          
          <v-list-item-subtitle>{{ user.username }}, You're awesome! </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list nav>
        <v-list-item 
          v-for="item in projects" 
          :key="item.title" 
          link 
        >
          <v-list-item-content 
            @click="projectItemSelected(item)"
          >
            <v-list-item-title>
              {{ item.title }}
            </v-list-item-title>
            <v-list-item-subtitle>
              {{ item.team.name }}
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar 
        app
        color="blue accent-1"
      >
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>

      <v-toolbar-title>Tasker Projects</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-menu
        left
        bottom
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            icon
            v-bind="attrs"
            v-on="on"
          >
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>

        <v-list>
          <v-list-item @click="goToAccountSetting">
            <v-list-item-title>
              Account 
            </v-list-item-title>
          </v-list-item>
          <v-list-item @click="logout">
            <v-list-item-title>
              Logout
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>

    <v-main>
      <div v-if="hasSelected">
        <div class="debug">
          <!-- <h4>DEBUG</h4> -->
          <p>
            <!-- {{ JSON.stringify(projects, null, '\n') }} -->
          </p>
        </div>
        <v-container>
          <h1>{{ selectedProject.title }}</h1>
        </v-container>
        <team-container :team="selectedProject.team"/>
        <v-divider></v-divider>
        <members :members="selectedProject.team.members"/>
        <v-divider></v-divider>
        <lists-container :taskList="selectedProject.tasklistSet"/>
        <v-divider></v-divider>
        <log-container />
      </div>
    </v-main>
  </v-app>
</template>

<script>
import Cookies from 'js-cookie';
import TeamContainer from './TeamContainer.vue';
import Members from './Members.vue';
import ListsContainer from './ListsContainer.vue';

import shared from '../shared.js';
import getProjects from './queries/getProjects';
import LogContainer from './LogContainer.vue';

  export default {
  components: { TeamContainer, Members, ListsContainer, LogContainer },
    data() {
      return {
        shared,
        serverUrl: `${window.location.protocol}//${window.location.hostname}:8000/graphql`,
        drawer: !null,
        projects: [],
        right: null,
        user: {
          username: '',
          fistname: '',
          lastname: '',
          token: '',
          refreshToken: '',
          pk: 0,
        },
        selectedProject: null,
        hasSelected: false,
      }
    },
    methods: {
      projectItemSelected(item) {
        shared.selectedProject = item;
        this.selectedProject = shared.selectedProject;

        this.hasSelected = !this.hasSelected;
      },

      async getUserData () {
        try {
        // get payload from token
          const payloadResponse = await fetch(
            this.serverUrl,
            {
              method: 'POST',
              headers: { 'Content-Type': 'application/json' },
              body: JSON.stringify({query: `
                mutation {
                  verifyToken(token: "${this.user.token}") { payload }
                }
              `}),
            }
          );
          const payloadData = await payloadResponse.json();
          const payload = await payloadData.data.verifyToken.payload;

          // get username from payload
          const username = payload.username;

          // get pk, firstname, lastname from username
          const userDataResponse = await fetch(
            this.serverUrl,
            {
              method: 'POST',
              headers: { 'Content-Type': 'application/json' },
              body: JSON.stringify({query: `
                query {
                  users(username: "${username}"){
                    edges{
                      node{
                        lastName
                        firstName
                        pk
                        username
                      }
                    }
                  }
                }
              `}),
            }
          );
          const userDataResponseData = await userDataResponse.json();
          const userNode = userDataResponseData.data.users.edges[0].node;

          this.user.username = username;
          this.user.fistname = userNode.firstName;
          this.user.lastname = userNode.lastName;
          this.user.pk = userNode.pk;

          this.getProjects();
        } catch (e) {
          alert('An error occured please login again');
          window.location = `/login`;
        }
      },

      async getProjects() {
        const data = await getProjects(
          shared.api,
          this.user.pk,
          this.user.token
        );
        shared.projects = await data.data.projectsByUser;
        this.projects = await shared.projects;
      },

      getUserTokens() {
        const remember = Cookies.get('remember');

        if (remember) {
          this.user.token = Cookies.get('auth_token');
          this.user.refreshToken = Cookies.get('refresh_token');
        } else if (remember === false) {
          this.user.token = sessionStorage.getItem('auth_token');
          this.user.refreshToken = sessionStorage.getItem('refresh_token');
        }
      },

      logout: () => {
        if (confirm('are you sure to logout?')) {
          // clear cache and session storage
          Cookies.remove('auth_token');
          sessionStorage.removeItem('auth_token');
          Cookies.remove('refresh_token');
          sessionStorage.removeItem('refresh_token');

          window.location = `/login`;
        }
      },

      goToAccountSetting: () => {
        window.location = `/account-settings`;
      },
    },
    created()  {
      //// manually set token for development
      // Cookies.set('remember', true);
      // Cookies.set(
      //   'auth_token',
      //   'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1c2VybmFtZSI6InJqZ2oiLCJleHAiOjE2MTg2ODc2OTUsIm9yaWdJYXQiOjE2MTg2Mjc3NTV9.z4c6noCsk02Ey6AYyFp4Wk54-bRUKHSJm7ItA5-W0Jw',
      //   { expires: 1 },
      //   );
      // Cookies.set(
      //   'refresh_token',
      //   '37da4bbbebdb64d20bfd2d41e32d866c77477b34',
      //   { expires: 7 },
      // );

      this.getUserTokens();
      this.getUserData();
    },
  }
</script>

<style scoped>
.debug {
  background-color: hsl(0, 98%, 65%);
  color: white;
  font-weight: 600;
  letter-spacing: 0.2ch;
}
