<template>
  <div class="company-container">
    <img :src="handleImage" alt="company image" class="rounded-circle">
    <div class="desc">
      <h3>{{ team.name }}</h3>
      <h5 id="description">{{ team.description }}</h5>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    team: Object,
  },
  computed: {
    handleImage: () => {
      try {
        return this.team.image;
      } catch (err) {
        console.log(`${err} file: TeamContainer 21:9`);
        return 'https://reactnativecode.com/wp-content/uploads/2018/02/Default_Image_Thumbnail.png';
      }
    },
  },
}
</script>

