<template>
  <v-card elevation="3" color="accent">
    <v-responsive min-width="300" max-width="300">
      <v-app-bar flat color="rgba(0, 0, 0, 0)">
        <v-toolbar-title>
          <strong>{{ taskData.title }}</strong>
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon>
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </v-app-bar>
      <v-card-subtitle>{{ taskData.description }}</v-card-subtitle>
      <v-card-text>
        <div>
          <v-container
            v-for="(item, index) in taskData.tasklistitemSet"
            :key="index"
          >
            <v-card
              elevation="2"
              m="3"
              @dblclick="toggleFinished(index)"
              :class="item.finished ? 'fnshd' : ''"
            >
            <v-app-bar flat color="rgba(0, 0, 0, 0)">
              <v-toolbar-title>{{ item.title }}</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-btn icon>
                <v-icon>mdi-dots-vertical</v-icon>
              </v-btn>
            </v-app-bar>
            <v-card-text>{{ item.description }}</v-card-text>
            </v-card>
          </v-container>
        </div>
        <v-form v-show="visible">
          <v-text-field placeholder="Add Item..." id="newItemField">
            <v-icon slot="append" @click="clear">mdi-close</v-icon>
          </v-text-field>
        </v-form>
        <v-btn block color="primary" @click="addNew">
          Add new +
        </v-btn>
      </v-card-text>
    </v-responsive>
  </v-card>
</template>

<script>
export default {
  props: {
    task: Object,
  },
  
  data() {
    return {
      taskData: Object,
      visible: false,
    }
  },

  methods: {
    toggleFinished(index) {
      let finished = this.taskData.tasklistitemSet[index].finished;
      this.taskData.tasklistitemSet[index].finished = !finished;
    },

    clear() {
      const el = document.getElementById("newItemField");
      el.value = '';
    },

    addNew() {
      this.visible = !this.visible
    },
  },

  created() {
    this.taskData = this.task;
  },
}
</script>

<style scoped>
.fnshd {
  text-decoration: line-through;
}
</style>