export default async (url, pk, token) => {
  const response = await fetch(
    url,
    {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        authorization: `JWT ${token}`
      },
      body: JSON.stringify({
        query: `
        query {
          projectsByUser(pk: ${pk} token: "${token}") {
            id
            title
            description
            team {
              id 
              email
              name
              description
              projectSet { id }
              members { id username firstName lastName }
            }
            creationDate
            tasklistSet {
              id
              title
              description
              tasklistitemSet {
                id
                title
                description
                creationDate
                dueDate
                finished
              }
            }
            logSet {
              id
              title
              description
              creator { id username }
              project { id title }
              creationDate
            }
          }
        }
      `}),
    }
  );
  return await response.json();
};